@import url(https://fonts.googleapis.com/css?family=Abril+Fatface);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.steps {
    clear: both;
    list-style: none;
    padding-left: 2%;
  }
  .steps li {
    margin: 2em 0;
    padding-top: 1em;
    display: block;
    position: relative;
    counter-increment: inst;
  }
  .steps li::before {
    content: counter(inst);
    
    background: rgba(255, 150, 0, 0.35);
    color: #fff;
    
    font-size: 1em;
    font-weight: 700;
    font-style: italic;
    text-shadow: 1px 1px rgba(255, 150, 0, 0.5);
    
    border-radius: 0 0.675em 0.675em 0;
    font-size: 1.5em;
    text-align: center;
    
    padding-top: 0;
    padding-left: 2.25%;
    left: -5%;
    top: -0.65em;
    height: 1.35em;
    width: 1.35em;
    position: absolute;
    
    transition: all 0.2s ease-in-out;
    
    z-index: -1;
  }

  @media (min-width: 33em) {
    .steps li:before {
      border-radius: 50%;
      font-size: 1.5em;
      height: 1.35em;
      margin-left: 2.5%;
      padding-left: 0;
      padding-top: 0;
      top: -0.15em;
      width: 1.35em;
      z-index: -1;
    }
  }
  /* ============= */

ol {
  list-style: none;
  counter-reset: my-awesome-counter;
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
}
ol li {
  font-size: 15px !important;
  counter-increment: my-awesome-counter;
  display: flex;
  width: 50%;
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
}
ol li::before {
  content: "@"counter(my-awesome-counter);
  font-weight: bold;
  font-size: 3rem;
  margin-right: 0.5rem;
  font-family: 'Abril Fatface', serif;
  line-height: 1;
}


i.icon, i.icons {
    font-size: 1.5em;
    margin-top: 1vw;
}

/* TodoItem */
.completed {
    text-decoration: line-through;
}

.edits {
    width: 68.5%;
}

.editsInput {
    width: 75% !important;
}

/* All */
hr {
    width: 30%;
    float: left;
}

ul {
    width: 30%;
}

li {
    margin-top: 1vw;
    margin-left: 1.5vw;
    width: 40vw;
}

@media (min-width: 420px) {
    hr {
        width: 70%;
        float: left;
    }
    
    ul {
        width: 70%;
    }
}

/* users list */
img {
    border-radius: 20%;
    width: 4vw;
}

.contentList {
    font-style: oblique;
    font-size: x-large;
    line-height: 1.6;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}

.ui.header>img+.content {
    display: contents;
}

.ui.vertical.menu .active.item {
    background: #03a9f496;
    width: 103%;
}

.ui.selection.list.list>.item:hover, .ui.selection.list>.item:hover {
    background: #03A9F4;
    color: rgb(255, 255, 255);
}

.ui.list .list>.item:hover .header, .ui.list>.item:hover .header {
    display: block;
    margin: 0;
    font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
    font-weight: 700;
    color: rgba(255, 255, 255, 0.87);
}

/* post cards */
.extra {
    background-color: #FFEB3B !important;
}

.ui.card>.content>.header, .ui.cards>.card>.content>.header {
    display: block;
    margin: '';
    font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
    color: #2196F3;
}

.ui.card>.extra, .ui.cards>.card>.extra {
    color: #3f51b5b0;
}
