i.icon, i.icons {
    font-size: 1.5em;
    margin-top: 1vw;
}

/* TodoItem */
.completed {
    text-decoration: line-through;
}

.edits {
    width: 68.5%;
}

.editsInput {
    width: 75% !important;
}

/* All */
hr {
    width: 30%;
    float: left;
}

ul {
    width: 30%;
}

li {
    margin-top: 1vw;
    margin-left: 1.5vw;
    width: 40vw;
}

@media (min-width: 420px) {
    hr {
        width: 70%;
        float: left;
    }
    
    ul {
        width: 70%;
    }
}
